:root {
  --scrollpos: 0;
}

body {
  height: 100%;
  background-color: #fff;
  flex-direction: column;
  font-family: Questrial, sans-serif;
  display: flex;
}

#logo {
  vertical-align: middle;
  padding-left: 10px;
  font-size: 30px;
}

header {
  background: #e0f9ff;
  justify-content: space-between;
  padding-top: 50px;
  display: flex;
}

nav {
  background-color: #e0f9ff;
  align-items: center;
  display: flex;
}

nav div li a {
  color: #110f0f;
  padding-right: 10px;
  font-size: 20px;
  font-weight: bold;
}

nav div li {
  display: inline-block;
}

nav div {
  list-style: none;
}

.main {
  flex-direction: row;
  flex-grow: 1;
  align-items: stretch;
  display: flex;
}

.footer {
  background-color: #e0f9ff;
  justify-content: center;
  display: flex;
}

.footer > div {
  margin-top: 2vh;
}

.river {
  width: 17%;
  height: 1500px;
  background-color: #e0f9ff;
  background-image: url("rive_inner.9614904a.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.rivertext {
  flex-direction: column;
  justify-content: space-around;
  display: flex;
}

.rivertext > div {
  text-align: center;
  margin-top: 4vh;
  font-family: Apple Chancery;
}

.articles {
  max-width: 100%;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-start;
  align-items: stretch;
  display: flex;
}

.intro {
  max-width: 75%;
  border: .25vh solid;
  flex-direction: row;
  margin: 2vh 10vw;
  padding: 1%;
  font-family: Montserrat;
  font-size: medium;
  display: flex;
}

#introText {
  height: -moz-fit-content;
  height: fit-content;
  text-align: center;
  font-size: large;
}

#introTextContainer {
  flex-wrap: wrap;
  place-content: center;
  display: flex;
}

.articles > div > div {
  margin: 1vh;
}

.boat {
  transform: translate(0, min(1300px, calc(1.5vh * var(--scrollpos))));
  width: 45%;
  height: 50px;
  background-color: #5cebf5;
  position: absolute;
  left: 25%;
  overflow: hidden;
}

.inner-boat {
  justify-content: center;
  display: flex;
}

#boatpng {
  width: 75%;
}

.portrait {
  width: 20vmin;
  border: thin solid silver;
  flex-flow: column;
  display: flex;
}

figcaption {
  color: #000;
  text-align: center;
  font: italic small sans-serif;
}

#svgContainer {
  width: 100%;
  height: 100%;
  position: relative;
}

#elements {
  transform: translateY(-60px);
}

.window-container {
  height: 43vmin;
  width: calc(33.33% - 10px);
  justify-content: top;
  background-color: #d3d3d3;
  flex-direction: column;
  display: flex;
}

.top-window {
  height: 100%;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.bottom-window {
  width: 18vmin;
  height: 5vmin;
  clip-path: polygon(15% 0%, 85% 0%, 100% 100%, 0% 100%);
  background-color: #deb887;
}

.inner-shutters {
  visibility: visible;
}

.outter-shutters {
  visibility: hidden;
}

.left-shutter-outter {
  height: 22vmin;
  width: 8vmin;
  clip-path: polygon(0 0, 100% 15%, 100% 85%, 0% 100%);
  background-color: #000;
  position: relative;
}

.left-shutter-outter:before {
  content: "";
  height: 21vmin;
  width: 7vmin;
  clip-path: polygon(0 0, 100% 15%, 100% 85%, 0% 100%);
  background: #fff;
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
}

.project-window {
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.proj-window-top {
  height: 15vmin;
  width: 20vmin;
  box-sizing: border-box;
  border-style: solid;
  border-color: #000;
  flex-direction: row;
  margin-top: 5vmin;
  display: flex;
}

.right-shutter-outter {
  height: 22vmin;
  width: 8vmin;
  clip-path: polygon(0% 15%, 100% 0%, 100% 100%, 0% 85%);
  background-color: #000;
  position: relative;
}

.right-shutter-outter:before {
  content: "";
  height: 21vmin;
  width: 7vmin;
  clip-path: polygon(0% 15%, 100% 0%, 100% 100%, 0% 85%);
  background: #fff;
  display: block;
  position: absolute;
  top: 2px;
  left: 2px;
}

.left-shutter-inner, .right-shutter-inner {
  width: 50%;
  height: 100%;
  z-index: 2;
  background-color: #fff;
  border-style: solid;
  border-color: #000;
}

.project-lists {
  width: 90%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  gap: 2vmin;
  margin-top: 5%;
  margin-left: 0;
  margin-right: 0;
  display: flex;
}

.projName {
  text-align: center;
  padding-top: 2vmin;
}

.projimg {
  height: 20vmin;
  width: 25vmin;
  z-index: 1;
  justify-content: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -70%);
}

.projimg > img {
  object-fit: contain;
  height: 95%;
  width: 90%;
  position: absolute;
}

.actualSvg {
  z-index: 2;
  position: relative;
}

.content {
  max-width: 75%;
  border: .25vh solid;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2vh 10vw;
  padding: 1%;
  display: flex;
}

.mymediablock {
  max-width: 75%;
  border: .25vh solid;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 1%;
  display: flex;
}

.mymedia {
  height: 20vh;
  width: 20vw;
  border: .25vh;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.emoji {
  height: 2.5em;
  width: 2.5em;
  background-color: plum;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: x-large;
  display: flex;
}

.projectLanding {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

.content > figure > img, .content > div > img {
  max-width: 75%;
}

.content > div, .content > figure {
  text-align: center;
}

@media screen and (max-width: 500px) {
  .river {
    display: none;
  }

  header {
    flex-direction: column;
    align-items: center;
  }

  .project-lists {
    flex-direction: column;
    justify-content: space-between;
  }

  .window-container {
    height: 50vmin;
    width: 50vmin;
    margin-top: 8vmin;
  }
}

